.list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 1rem;
  grid-row-gap: 1rem;
  margin: 0 auto;
  padding: 0 1rem;
  max-width: 1100px;
  list-style: none;
}

@media (max-width: 1100px) {
  .list {
      grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 730px) {
  .list {
      grid-template-columns: 1fr;
  }
}


