.filter-list {
  margin-bottom: 4rem;
}

.filter-list__logo {
  width: 4rem;
  height: 4rem;
}

.filter-list__h1 {
  color: #ffffff;
  font-size: 1.2rem;
  margin: 0;
  font-weight: normal;
}

.filter-list__nav {
  padding: 0.75rem 1rem;
  background-color: #141b2a;
  -webkit-box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
}

.filter-list__heading {
  color: #ffffff;
}

.filter-list__search {
  padding: 1rem;
  width: 100%;
  border: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 4px;
  font-family: inherit;
  font-size: inherit;
  color: #ffffff;

  background-color: rgba(255, 255, 255, 0.2);
  -webkit-transition: 300ms ease;
  -o-transition: 300ms ease;
  transition: 300ms ease;
  -webkit-transition-property: background-color, color, -webkit-box-shadow;
  transition-property: background-color, color, -webkit-box-shadow;
  -o-transition-property: background-color, box-shadow, color;
  transition-property: background-color, box-shadow, color;
  transition-property: background-color, box-shadow, color, -webkit-box-shadow;
}

.filter-list__search:focus {
  color: #222222;
  background-color: #ffffff;
  -webkit-box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
}

.filter-list__container {
  max-width: 1100px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 64px 2fr 1fr;
  grid-column-gap: 2rem;
  -ms-flex-align: center;
      align-items: center;
}

.filter-list__filters {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.filter-list__filters-item {
  display: inline-block;
}

.filter-list__sort {
  margin: 0 auto;
  padding: 0 1rem;
  max-width: 1100px;
  text-align: right;
}

@media (max-width: 900px) {
  .filter-list__search {
    grid-column-start: 2;
    grid-column-end: 4;
  }

  .filter-list__filters {
    grid-column-start: 1;
    grid-column-end: 4;

    overflow-x: auto;
    margin: 0 -1rem;
    padding: 0 1rem;
  }

  .filter-list__filters-item:first-of-type {
      border-top: 0;
  }
}


