
.filter-list__filters-item > .dropdown > .dropdown-toggle {
  padding: 0.5rem 1rem;
  width: 100%;
  border: 0;
  background-color: transparent;
  color: #ffffff;
  font-family: inherit;
  font-size: inherit;
  white-space: nowrap;
}

.dropdown > .dropdown-menu {
  display: none;
  position: absolute;
  list-style: none;
  margin: 0.25rem 0 0 0;
  padding: 0.25rem 0;
  -webkit-box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  background-color: #ffffff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 1;
}

.dropdown-menu > li > a {
  display: block;
  padding: 0.5rem 1rem;
  text-decoration: none;
  color: #222222;
  font-size: 1rem;
}

.dropdown-menu > li > a:hover {
  background-color: #eee;
}

.open.btn-group > .dropdown-menu {
  display: block;
}

.caret{
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: .255em;
  vertical-align: .255em;
  content: "";
  border-top: .3em solid;
  border-right: .3em solid transparent;
  border-bottom: 0;
  border-left: 0.3em solid transparent;
}

@media (max-width: 900px) {
  .filter-list__filters-item > .dropdown > .dropdown-menu {
    left: 0;
    width: 100%;
    margin-top: 0.75rem;
    border-radius: 0;
  }

  .dropdown-menu > li > a {
    padding: 1rem;
  }
}


