.filter-list__sort > .dropdown {
  display: inline-block;
  margin: 1rem 0;
  position: relative;
}

.filter-list__sort > .dropdown > .dropdown-toggle {
  border: none;
  font-family: inherit;
  font-size: inherit;
  background: transparent;
  padding: 1rem;
}

.filter-list__sort > .dropdown > .dropdown-menu {
  right: 0;
  width: 13rem;
}

@media (max-width: 900px) {
  .filter-list__sort > .dropdown,
  .filter-list__sort > .dropdown > .dropdown-toggle,
  .filter-list__sort > .dropdown > .dropdown-menu {
    margin: 0;
  }
}


