.player {
  overflow: hidden;
  border-radius: 4px;
  -webkit-box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  background-color: #ffffff;
}

.player__bg {
  height: 7rem;
  background-color: #555555;
  background-size: cover;
  background-position: center;
}

.player__img {
  width: 4rem;
  height: 4rem;
  background-size: cover;
  background-position: center;
  border-radius: 4rem;
  margin-top: -2rem;
  margin-left: 1rem;
  border: 4px solid #ffffff;
  background-color: #dddddd;
}

.player__team {
  float: right;
  margin-top: 0.75rem;
  margin-right: 0.75rem;
  padding: 0.1rem 0.75rem;
  background-color: #dddddd;
  border-radius: 4rem;
}

.player__hl {
  margin: 1.25rem;
}

.player__ht {
  font-size: 1.5rem;
  display: block;
}

.player__hd {
  font-size: 1rem;
  display: block;
  margin-left: 0.1rem;
  color: #333333;
}

.player__dl {
  margin: 1.25rem;
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-column-gap: 1rem;
}

.player__dl-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.player__dt {
  color: #444444;
}

.player__dd {
  margin: 0;
  font-weight: bold;
}


